import React, {Component} from 'react';
import logo from './engie logo.png';
import biking from './biking img.png';
import swimming from './swimming.png';
import walking from './walking.jpg';
import library from './thompson inside.jpg';
import {Link} from "react-router-dom";
import { IgrRadialGaugeModule } from "igniteui-react-gauges/ES5/igr-radial-gauge-module";
import { IgrRadialGauge } from "igniteui-react-gauges/ES5/igr-radial-gauge";

IgrRadialGaugeModule.register();



export default class Thompson extends React.Component{

    
    render(){
        return(
<div style={{background: "#2193b0", background: "-webkit-linear-gradient(to bottom, #6dd5ed, #2193b0)", background: "linear-gradient(to bottom, #6dd5ed, #2193b0)"}}>
<div>
    <nav class="navbar fixed-top navbar-expand-lg navbar-light bg-light">
<a class="navbar-brand">
  <img src={logo} width="130" height="70" class="d-inline-block align-top" alt="" alt="Brand" />
  </a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav ml-auto" style={{paddingRight:"5.5em"}}>
      <li class="nav-item active">
        <Link class="nav-link" to="/">Home</Link>
      </li>
      <li class="nav-item">
      <Link class="nav-link"to="/directory">Directory</Link>
      </li>
      <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          information
        </a>
        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
          <a target="_blank" href="https://www.ucribs.com/blog-post/55-ways-college-students-can-save-energy-reduce-waste-go-green-mega-list/" class="dropdown-item" to="/">Energy Saving Techniques</a>
          <a target="_blank" href="https://greenbuckeyes.osu.edu/student-engagement/organizations/" class="dropdown-item" to="/">Get Involved</a>
        </div>
      </li>
    </ul>
  </div>
</nav>

<div class="card mb-3">
  <img src={library} class="card-img-top" alt="..."/>
</div>



</div>
<div class="container">
<div class="row" style={{alignItems:"center", justifyContent: "center"}}>
<div >
 <IgrRadialGauge
    backingShape="Fitted"
    backingBrush="#fcfcfc"
    backingOutline="DodgerBlue"
    backingOversweep={5}
    backingCornerRadius={10}
    backingStrokeThickness={5}
    backingOuterExtent={0.8}
    backingInnerExtent={0.15}
    scaleStartAngle={135} scaleEndAngle={50}
    height="700px" width="700px"
    minimumValue={0} value={3.8}
    maximumValue={4} interval={.3}>
</IgrRadialGauge>
</div>
<p class="display-3" style={{marginTop:"-1.8em"}}>38,087,140 kWH</p>
<p class="display-5" >Today's Power Usage</p>
</div>

</div>
<div >

</div>
<div class="card-group">
  <div class="card">
    <img src={biking} class="card-img-top" alt="..."/>
    <div class="card-body">
      <h5 class="card-title">3.25 Years</h5>
      <p class="card-text">It would take you 3.25 years to power Thompson for an hour by Biking</p>
    </div>
  </div>
  <div class="card">
    <img src={walking} style={{width:"75%"}}class="card-img-top" alt="..."/>
    <div class="card-body">
      <h5 class="card-title">3.5 Years</h5>
      <p class="card-text">It would take you 3.5 years from walking to power Thompson for an hour by walking</p>
    </div>
  </div>
  <div class="card">
    <img src={swimming} class="card-img-top" alt="..."/>
    <div class="card-body">
      <h5 class="card-title">3000 Laps</h5>
      <p class="card-text">You would need to swim 3,000 laps to power Thompson for an hour by Swimming!</p>
    </div>
  </div>
</div>
<div class="card mb-3" style={{width:"100%"}}>
  <div class="row no-gutters">
    <div class="col-md-4">
      <img src={logo} class="card-img" alt="..."/>
    </div>
    <div class="card-body">
    <blockquote class="blockquote mb-0">
      <p>“Knowing Is Not Enough; We Must Apply. Wishing Is Not Enough; We Must Do.”</p>
      <footer class="blockquote-footer">Johann Wolfgang Von Goethe </footer>
    </blockquote>
  </div>
  </div>
</div>
</div>
        )
}
}