import React, {Component} from 'react';
import logo from './engie logo.png';
import {Link} from "react-router-dom";


export default class Directory extends React.Component{
    constructor (props) {
        super(props);
            this.items = [
             'Thompson Library',
             'RPAC',
             'University Hall',
            ];
            this.state = {
                suggestions: [],
                text: '',
            };
        }



        onTextChanged = (e) => {
            const value = e.target.value;
            let suggestions = [];
            if(value.length > 0){
                const regex = new RegExp(`^${value}`, 'i');
                suggestions = this.items.sort().filter(v => regex.test(v));
            }
            this.setState(() => ({suggestions, text: value}));
        }

        suggestionSelected (value){
            this.setState(() => ({
                text:value,
                suggestions: [],
            }))
        }

        renderSuggestions (){
            const {suggestions} = this.state;
            
            if(suggestions.length === 0){
                return null;
            }


            return(
                <ul style={{listStyleType:"none", marginTop:"2em"}}>
                    
                    {suggestions.map((item) => <li onClick={() => this.suggestionSelected(item)}>{item}</li>)}
                    
                </ul>
            );
            
        }

        
    
    render(){
        const {text} = this.state;
        return(
<div>
<nav class="navbar fixed-top navbar-expand-lg navbar-light bg-light">
<a class="navbar-brand">
  <img src={logo} width="130" height="70" class="d-inline-block align-top" alt="" alt="Brand" />
  </a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav ml-auto" style={{paddingRight:"5.5em"}}>
      <li class="nav-item">
        <Link class="nav-link" to="/">Home</Link>
      </li>
      <li class="nav-item active">
      <Link class="nav-link"to="/directory">Directory</Link>
      </li>
      <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          information
        </a>
        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
          <a target="_blank" href="https://www.ucribs.com/blog-post/55-ways-college-students-can-save-energy-reduce-waste-go-green-mega-list/" class="dropdown-item" to="/">Energy Saving Techniques</a>
          <a target="_blank" href="https://greenbuckeyes.osu.edu/student-engagement/organizations/" class="dropdown-item" to="/">Get Involved</a>
        </div>
      </li>
    </ul>
  </div>
</nav>


  <div class="container"style={{marginTop:"20em", width:"50%", alignItems: "center", justifyContent:"center"}}>          

<input class="form-control mr-sm-2" placeholder="Search" aria-label="Search" value={text} onChange={this.onTextChanged} type="text"
/> 
  <Link to={this.state}> {this.renderSuggestions()} </Link>
</div>
<div class="card mb-3" style={{width:"100%", marginTop:"20em"}}>
  <div class="row no-gutters">
    <div class="col-md-4">
      <img src={logo} class="card-img" alt="..."/>
    </div>
    <div class="card-body">
    <blockquote class="blockquote mb-0">
      <p>“Knowing Is Not Enough; We Must Apply. Wishing Is Not Enough; We Must Do.”</p>
      <footer class="blockquote-footer">Johann Wolfgang Von Goethe </footer>
    </blockquote>
  </div>
  </div>
</div>
</div>
        )
}
}